describe('drag-drop actions', () => {
  it('should create an action to select a team member id', () => {
    const memberId = '18000003';
    const expectedAction = {
      type: ActionTypes.SELECT_TEAM_MEMBER,
      payload: { memberId }
    };
    expect(actions.selectTeamMember(memberId)).toEqual(expectedAction);
    // expect(reducer(initialState, selectTeamMember(memberId))).toMatchSnapshot();
  });
  it('should create an action to remove a team member id', () => {
    const memberId = '18000003';
    const expectedAction = {
      type: ActionTypes.UNSELECT_TEAM_MEMBER,
      payload: { memberId }
    };
    expect(actions.unSelectTeamMember(memberId)).toEqual(expectedAction);
  });
});

describe('drag-drop reducers', () => {
  it('should return the initial state', () => {
    expect(reducer(undefined, [])).toEqual([
      {
        teamMembers: [],
        error: ''
      }
    ]);
  });
});
